import React, { useEffect, useState } from 'react'
import {
    Card, Row, Col, Button,
    Space, message,
    Table, Modal,
} from 'antd';

import {
    PlusOutlined,
    EyeOutlined,
    EditOutlined,
    DeleteOutlined,
} from '@ant-design/icons';
import FilterList from './FilterList';
import hasAuthObject from '../../helpers/hasAuthObject';
import Api from '../../helpers/api';
import handleApiError from '../../helpers/handleApiError';
const api = new Api();
// setup auth object config for this page
const authobjectCfg = {
    view: 'material-sourcing-view', //button display detail
    create: 'material-sourcing-create', //button add
    update: 'material-sourcing-update', //button edit, 
    delete: 'material-sourcing-delete', //button delete
}

const List = (props) => {

    const [selectedRowKeys, setSelectedRowKeys] = useState([])
    const [loading, setLoading] = useState(false)
    const [data, setData] = useState([])
    const [pagination, setPagination] = useState({})
    const [filters, setFilters] = useState({})

    useEffect(() => {
        fetch({ ...filters })
    }, [filters])

    const fetch = (params = {}) => {
        setLoading(true)
        api.getMasterMaterialSourcingList({ ...params })
            .then(response => {
                const data = response.data;

                let pagination = {}

                pagination.current = data.current_page
                pagination.total = data.total;
                pagination.pageSize = parseInt(data.per_page);

                setData(data.data)
                setPagination(pagination)
                setLoading(false)
            })
            .catch(error => {
                setLoading(false)
                handleApiError(error, props)
            })
    }

    const handleTableChange = (paginate, sorter) => {
        const pager = { ...pagination };
        pager.current = paginate.current;

        setPagination(pager)

        fetch({
            per_page: paginate.pageSize,
            page: paginate.current,
            sort_field: sorter.field,
            sort_order: sorter.order === 'ascend' ? 'asc' : 'desc',
            ...filters,
        });
    }

    const handleFilter = (value) => {
        setFilters(value)
    }

    const handleEdit = () => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);

        if (selectedRowKeys.length > 1) {
            Modal.warning({
                title: `Anda memilih ${selectedRowKeys.length} data, silahkan hanya pilih 1 data`,
            });
        } else {
            props.history.push(`/material-sourcing/edit/${selectedRowKeys[0]}`)
        }
    }

    const handleDelete = () => {
        Modal.confirm({
            title: `Delete ${selectedRowKeys.length} data?`,
            okText: 'Yes',
            okType: 'danger',
            cancelText: 'No',
            onOk: () => {
                massDeleteData()
            },
            onCancel: () => {
                console.log('Cancel');
            },
        });
    }

    const handleView = () => {
        if (selectedRowKeys.length > 1) {
            Modal.warning({
                title: `Anda memilih ${selectedRowKeys.length} data, silahkan hanya pilih 1 data`,
            });
        } else {
            props.history.push(`/material-sourcing/view/${selectedRowKeys[0]}`)
        }
    }

    const massDeleteData = () => {
        api.massDeleteMaterialSourcing({ id: selectedRowKeys })
            .then(response => {
                // console.log(response.data)
                message.success('Delete data success');
                fetch();
                setSelectedRowKeys([])
            })
            .catch(error => {
                //console.log(error.response)
                message.error('Failed to delete');
                handleApiError(error, props)
            });
    }

    //on check table item
    const onSelectChange = (selectedRowKeys) => {
        setSelectedRowKeys(selectedRowKeys)
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
        // type: 'radio'
    };

    const hasSelected = selectedRowKeys.length > 0;

    const columns = [
        {
            title: 'Room',
            dataIndex: 'room',
            sorter: true,
            render: (room) => {
                return room ? room.name : '-'
            },
        },
        {
            title: 'Plant',
            dataIndex: 'room',
            sorter: true,
            render: (room) => {
                return room ? room.plant.name : '-'
            },
        },
        {
            title: 'Material',
            dataIndex: 'material',
            sorter: true,
            render: (material) => {
                if (material) { return material.material_code }
            },
        },
        {
            title: 'Description',
            dataIndex: 'material',
            sorter: true,
            render: (material) => {
                if (material) { return material.description }
            },
        },
        {
            title: 'Classification',
            dataIndex: 'material',
            sorter: true,
            render: (material) => {
                if (material) { return material.classification.name }
            },
        },
        {
            title: 'Minimum Stock',
            dataIndex: 'minimum_stock',
            sorter: true,
            className: 'col-number',
        },
    ]

    return (
        <Card>
            <Space direction="vertical" size="large" style={{ width: '100%' }}>
                <Row>
                    <Col lg={24}>
                        <FilterList filter={handleFilter} />
                    </Col>
                </Row>
                <Row>
                    <Col lg={24}>
                        <Space>
                            {hasAuthObject(authobjectCfg.create) &&
                                <Button
                                    type="primary"
                                    onClick={() => { props.history.push('/material-sourcing/add') }}
                                    icon={<PlusOutlined />}
                                >Add</Button>
                            }

                            {hasAuthObject(authobjectCfg.update) &&
                                <Button
                                    onClick={() => handleEdit()}
                                    disabled={!hasSelected}
                                    icon={<EditOutlined />}
                                >Edit</Button>
                            }

                            {hasAuthObject(authobjectCfg.view) &&
                                <Button
                                    onClick={() => handleView()}
                                    disabled={!hasSelected}
                                    icon={<EyeOutlined />}
                                >View</Button>
                            }

                            {hasAuthObject(authobjectCfg.delete) &&
                                <Button
                                    danger
                                    onClick={() => handleDelete()}
                                    disabled={!hasSelected}
                                    icon={<DeleteOutlined />}
                                >Delete</Button>
                            }
                        </Space>
                    </Col>
                </Row>
                <Row>
                    <Col lg={24} xs={24}>
                        <Table
                            loading={loading}
                            rowSelection={hasAuthObject(authobjectCfg.view || authobjectCfg.update || authobjectCfg.delete) ? rowSelection : null}
                            columns={columns}
                            rowKey={record => record.id}
                            onChange={handleTableChange}
                            dataSource={data}
                            pagination={{
                                pageSizeOptions: ['20', '50', '100', '200'],
                                showSizeChanger: true,
                                showQuickJumper: true,
                                showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
                                ...pagination
                            }}
                            scroll={{ y: 300, x: 1000 }} />
                    </Col>
                </Row>
            </Space>
        </Card>
    )
}

export default List