import React, { useEffect, useState } from 'react'
import {
    Row, Col, Button,
    Space, Collapse, DatePicker, Select
} from 'antd';

import {
    SearchOutlined,
    ReloadOutlined
} from '@ant-design/icons';
import moment from 'moment';
import Api from '../../helpers/api';
import handleApiError from '../../helpers/handleApiError';

const { RangePicker } = DatePicker;
const { Option } = Select;
const api = new Api;

const FilterList = (props) => {

    const [plant, setPlant] = useState([])
    const [room, setRoom] = useState([])
    const [valueSearch, setValueSearch] = useState({
        plant_id: [],
        room_id: [],
        created_at: [],
    })

    useEffect(() => {
        fetchPlant()
        fetchRoom()
        getDataBySavedFilter()
    }, [])

    // plant
    const fetchPlant = (q) => {
        api.getPlant({ q: q }).then(response => {
            const { data } = response.data;
            setPlant(data);
        }).catch(err => {
            console.log(err)
            handleApiError(err, props)
        })
    };

    const handleSearchPlant = (value) => {
        fetchPlant(value)
    }

    // room
    const fetchRoom = (q) => {
        api.getRoom({ q: q }).then(response => {
            const { data } = response.data;
            setRoom(data);
        }).catch(err => {
            console.log(err)
            handleApiError(err, props)
        })
    };

    const handleSearchRoom = (value) => {
        fetchRoom(value)
    }

    const handleInput = (value, name) => {
        setValueSearch({ ...valueSearch, [name]: value })
    }

    const getDataBySavedFilter = () => {
        const dataLocalStorage = JSON.parse(localStorage.getItem('filter_material_sourcing'))
        if (dataLocalStorage) {
            const filtersKeys = Object.keys(dataLocalStorage).length
            if (filtersKeys > 0) {
                if (dataLocalStorage.created_at) {
                    dataLocalStorage.created_at.map((item, index) => {
                        dataLocalStorage.created_at[index] = moment.utc(item, 'YYYY-MM-DD')
                    })
                }
                setValueSearch(dataLocalStorage)
                props.filter(dataLocalStorage)
            }
        }
    }

    const handleFilter = () => {
        if (valueSearch.created_at) {
            valueSearch.created_at.map((item, index) => {
                valueSearch.created_at[index] = moment.utc(item, 'YYYY-MM-DD')
            })
        }

        props.filter(valueSearch)
        // localStorage.setItem('filter_material_sourcing', JSON.stringify(valueSearch))
    }

    const handleReset = () => {
        // localStorage.removeItem('filter_material_sourcing');
        setValueSearch({})
        props.filter({})
    }

    return (
        <Collapse defaultActiveKey={["1"]}>
            <Collapse.Panel header={["Filter Material Sourcing"]} key="1" >
                <Row>
                    <Col lg={24}>
                        <Row>
                            <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onSearch={handleSearchRoom}
                                    onChange={(value) => handleInput(value, 'room_id')}
                                    name="room"
                                    placeholder="Room"
                                    value={valueSearch.room_id}>

                                    {room.map(item => (
                                        <Option key={item.id} value={item.id}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Col>
                            <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onSearch={handleSearchPlant}
                                    onChange={(value) => handleInput(value, 'plant_id')}
                                    name="plant"
                                    placeholder="Plant"
                                    value={valueSearch.plant_id}>

                                    {plant.map(item => (
                                        <Option key={item.id} value={item.id}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Col>
                            {/* <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onSearch={handleSearchVendor}
                                    onChange={handleChangeVendor}
                                    name="vendor"
                                    placeholder="Vendor"
                                    value={valueSearch.vendor_id}>

                                    {vendor.map(item => (
                                        <Option key={item.id} value={item.id}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Col> */}
                            <Col style={{ padding: '5px' }} lg={6} xs={24}>
                                <RangePicker
                                    onChange={(value) => handleInput(value, 'created_at')}
                                    value={valueSearch.created_at}
                                    placeholder={['Created at', 'Created at']}
                                    style={{ width: '100%' }} />
                            </Col>
                        </Row>
                    </Col>
                </Row>

                <Space style={{ padding: '5px' }}>
                    <Button type="primary"
                        icon={<SearchOutlined />}
                        onClick={handleFilter}
                    >Search</Button>

                    <Button
                        icon={<ReloadOutlined />}
                        onClick={handleReset}
                    >Reset</Button>
                </Space>
            </Collapse.Panel>
        </Collapse>
    )
}

export default FilterList